import React, {useState} from 'react'
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CInvalidFeedback,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {NotificationManager} from 'react-notifications';
import {checkUserActivation, completeRegistration} from '../../../services/userManagementService'
import {Link, useHistory} from 'react-router-dom'


const Activation = () => {



  const [activationNo, setActivationNo] = useState('');
  const [activationCheck, setActivationCheck] = useState(false);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [passwordConfirm, setPasswordConfirm] = useState('');


  const [userNameInvalid, setUsernameInvalid] = useState(false);
  const [passwordInvalid, setPasswordInvalid] = useState(false);
  const [confirmInvalid, setConfirmInvalid] = useState(false);
  const [activationInvalid, setActivationInvalid] = useState(false);



  const handleChange = (event) => {

    var name = event.target.name;
    var val = event.target.value;

    if (name == "username") {
      setUsername(val);
      if (val == "") {
        setUsernameInvalid(true);
      } else {
        setUsernameInvalid(false);
      }
    }
    if (name == "password") {
      setPassword(val);
      if (val == "") {
        setPasswordInvalid(true);
      } else {
        setPasswordInvalid(false);
      }
    }
    if (name == "passwordConfirm") {
      setPasswordConfirm(val);
      if (val != password) {
        setConfirmInvalid(true);
      } else {
        setConfirmInvalid(false);
      }
    }
    if (name == "activationNo") {
      setActivationNo(val);
      if (val == "") {
        setActivationInvalid(true);
      }
      else {
        setActivationInvalid(false);
      }
    }
    // console.log("----------val ------ " + event.target.value)
  }


  console.log("status " + activationCheck)
  const history = useHistory();

  const handleActivation = () => {
    console.log("---------- Activation Submit")
    if (activationNo.length == 0) {
      setActivationInvalid(true);
    }
    else {
      checkUserActivation(activationNo).then(response => {
        if (response.status) {
          NotificationManager.success(response.message, 'Success', 3000);
          setActivationCheck(true);

          // history.push("/login");
        } else {
          NotificationManager.error(response.message, 'Failed!', 3000);
        }
      })
        .catch(err => {
          console.log(err);
          if (err.response.status == 403) {
            NotificationManager.error("You are not allowed to perform this task", 'Failed!', 3000);
          } else {
            NotificationManager.error("Unable to try, check your connection", 'Failed!', 3000);
          }
        });

    }


  }

  const handleRegistration = () => {

    if (username.length == 0) {
      setUsernameInvalid(true)
    } else if (password.length == 0) {
      setPasswordInvalid(true)
    } else if (confirmInvalid) {
      confirmInvalid(true)
    } else {
      const insertDto = {
        "activationNo": activationNo,
        "userName": username,
        "password": password
      }
      completeRegistration(insertDto).then(response => {
        if (response.status) {
          NotificationManager.success(response.message, 'Success', 3000);
          history.push("/login");
        } else {
          NotificationManager.error(response.message, 'Failed!', 3000);
        }
      })
        .catch(err => {
          console.log(err);
          if (err.response.status == 403) {
            NotificationManager.error("You are not allowed to perform this task", 'Failed!', 3000);
          } else {
            NotificationManager.error("Unable to try, check your connection", 'Failed!', 3000);
          }
        });
    }


    console.log("---------- Activation Submit")




  }

  return (
    <>
      {activationCheck ?
        <div className="c-app c-default-layout flex-row align-items-center">
          <CContainer>
            <CRow className="justify-content-center">
              <CCol md="9" lg="7" xl="6">
                <CCard className="mx-4">
                  <CCardBody className="p-4">

                    <h1>Register</h1>
                    <p className="text-muted">Create your username and password</p>

                    <CInputGroup className="mb-3" >
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-user" />
                        </CInputGroupText>

                      </CInputGroupPrepend>
                      <CInput
                        id="username"
                        size="large"
                        name="username"
                        invalid={userNameInvalid}
                        value={username}
                        placeholder="Username"
                        onChange={handleChange}
                        onKeyDown={(e) => {
                          if (e.key === 'Enter') {
                            handleRegistration();
                          }
                        }}
                      />
                      <CInvalidFeedback className="help-block">
                        Please provide a valid username
                  </CInvalidFeedback>

                    </CInputGroup>
                    <CInputGroup className="mb-3">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-lock-locked" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput
                        type="password"
                        placeholder="Password"
                        autoComplete="new-password"
                        name="password"
                        value={password}
                        invalid={passwordInvalid}
                        onChange={handleChange}
                        onKeyDown={(e) => {
                          if (e.key === 'Enter') {
                            handleRegistration();
                          }
                        }}
                      />
                      <CInvalidFeedback className="help-block">
                        Please provide a valid password
                  </CInvalidFeedback>
                    </CInputGroup>
                    <CInputGroup className="mb-4">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-lock-locked" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput
                        type="password"
                        placeholder="Repeat password"
                        autoComplete="new-password"
                        name="passwordConfirm"
                        value={passwordConfirm}
                        invalid={confirmInvalid}
                        onChange={handleChange}
                        onKeyDown={(e) => {
                          if (e.key === 'Enter') {
                            handleRegistration();
                          }
                        }}
                      />
                      <CInvalidFeedback className="help-block">
                        Passwords do not match
                  </CInvalidFeedback>
                    </CInputGroup>
                    <div className="d-flex justify-content-center" md="6">
                      <button
                        type="submit"
                        className="btn btn-outline-success m-2 btn-md"
                        onClick={handleRegistration}
                      >Register Credentials
                          </button>
                    </div>

                  </CCardBody>

                </CCard>
              </CCol>
            </CRow>
          </CContainer>
        </div>

        :
        <div className="c-app c-default-layout flex-row align-items-center">
          <CContainer>
            <CRow className="justify-content-center">
              <CCol md="9" lg="7" xl="6">
                <CCard className="mx-4">
                  <CCardBody className="p-4">
                    <h1>Account Activation</h1>
                    <p className="text-muted">Enter your activation Number</p>
                    <CInputGroup className="mb-3">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-lock-locked" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput
                        type="number"
                        id="text-input"
                        onChange={handleChange}
                        name="activationNo"
                        value={activationNo}
                        placeholder="Activation Number"
                        invalid={activationInvalid}
                        onKeyDown={(e) => {
                          if (e.key === 'Enter') {
                            handleActivation();
                          }
                        }}
                      />
                      <CInvalidFeedback className="help-block">
                        Please provide a valid username
                  </CInvalidFeedback>
                    </CInputGroup>
                    <div className="d-flex justify-content-center" md="6">
                      <button
                        type="button"
                        className="btn btn-outline-success m-2 btn-md"
                        onClick={handleActivation}
                      >
                        Activate Account
                          </button>
                    </div>

                  </CCardBody>
                  <CCardFooter>
                    <CRow>
                      <CCol xs="4">

                      </CCol>
                      <CCol xs="8" className="text-right">Already Registered?
                      <Link to="/login">
                          <CButton color="link" className="px-0" style={{ marginLeft: 10, marginRight: 10 }}> Login here</CButton>
                        </Link>
                      </CCol>
                    </CRow>
                  </CCardFooter>
                </CCard>
              </CCol>
            </CRow>
          </CContainer>
        </div>
      }
    </>
  )
}

export default Activation
